import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    login: false,
    s0: false,
    s1: false,
    s2: false,
    s3: false,
    s4: false,
  },
  getters: {
    isLogin(state) {
      return state.login
    },
    s0(state) {
      return state.s0
    },
    s1(state) {
      return state.s1
    },
    s2(state) {
      return state.s2
    },
    s3(state) {
      return state.s3
    },
    s4(state) {
      return state.s4
    }
  },
  mutations: {
    login(state) {
      state.login = true
    },
    logout(state) {
      state.login = false
    },
    setS(state, payload) {
      state.s0 = payload.s0
      state.s1 = payload.s1
      state.s2 = payload.s2
      state.s3 = payload.s3
      state.s4 = payload.s4
    }
  },
  actions: {
  },
  modules: {
  }
})

export default store