import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '../store'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    beforeEnter: (from, to, next) => {
      if (!store.getters.isLogin) {
        return next('/login')  
      }
      return next()
    }
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import(/* webpackChunkName: "home" */ '../views/Home.vue'),
  },
  {
    path: '/live',
    name: 'Live',
    component: () => import(/* webpackChunkName: "live" */ '../views/Live.vue'),
    beforeEnter: (from, to, next) => {
      if (!store.getters.isLogin) {
        return next('/login')  
      }
      return next()
    }
  },
  {
    path: '/each',
    name: 'Each',
    component: () => import(/* webpackChunkName: "each" */ '../views/Each.vue'),
    beforeEnter: (from, to, next) => {
      if (!store.getters.isLogin) {
        return next('/login')  
      }
      return next()
    }
  },
  {
    path: '/stat',
    name: 'Stat',
    component: () => import(/* webpackChunkName: "stat" */ '../views/Stat.vue'),
    beforeEnter: (from, to, next) => {
      if (!store.getters.isLogin) {
        return next('/login')  
      }
      return next()
    }
  },
  {
    path: '/report',
    name: 'Report',
    component: () => import(/* webpackChunkName: "report" */ '../views/Report.vue'),
    beforeEnter: (from, to, next) => {
      if (!store.getters.isLogin) {
        return next('/login')  
      }
      return next()
    }
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
